.navb {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    background-color: whitesmoke;
    padding: 0 1rem;
    border-bottom: 1px solid darkgray;
}

.navb-nav {
    max-width: 1500px;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.navb-item {
    display: flex;
    align-items: center;
    justify-content: center;
}

