.banner-back {
    position: relative;
    display: flex;
    width: 100%;
    height: 350px;
}

.banner-img-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    width: 100%;
    overflow: hidden;
}

.banner-img {
    width: auto;
    height: auto;
    max-width: 100vw;
    max-height: 500px;
}